import React, { useState } from 'react';
import ClearFix from "../components/common/ClearFix";
import DefaultLayout from "../components/layout/DefaultLayout";
import { useNavigate } from 'react-router-dom'; //  useNavigate  useHistory



const HomePage = () => {

    const [inputValue, setInputValue] = useState('');
    const navigate = useNavigate(); //  useNavigate  useHistory

    const handleButtonClick = () => {
        navigate("/Personal"); // 
    };
    const handleInputChange = (event) => {
        setInputValue(event.target.value);
    };

    const handleSubmit = (event) => {
        event.preventDefault();
        console.log(inputValue);
    };
    
    return (
        <DefaultLayout fixedHeader>           
            <ClearFix height="100px"/>  

            <div>
                <h1>Home Page</h1>                                                                               
            </div>

            <form onSubmit={handleSubmit}>
                <input type="text" placeholder="" value={inputValue} onChange={handleInputChange} />
                <button type="submit" onClick={handleButtonClick}></button>
            </form>
            
            <ClearFix height="500px"/>
        </DefaultLayout>
    );
};

export default HomePage;