import React, { useState } from 'react';
import DefaultLayout from "../components/layout/DefaultLayout";
import ClearFix from "../components/common/ClearFix";
import { DatePicker, Space, InputNumber, Select } from 'antd';
import { Button, Flex, Input,Card } from 'antd';

const ItemsList = () => {
    const [size] = useState('large'); // default is 'middle'

    const handleChange = (value) => {
        console.log(`selected ${value}`);
    };
    const onChangeDatePicker = (date, dateString) => {
        console.log(date, dateString);
    };

    // const gridStyle = {
    //     width: '25%',
    //     textAlign: 'center',
    //   };


    return (
        <DefaultLayout fixedHeader>           
            <ClearFix height="100px"/>             
           
            <Card title="">
            <Flex vertical gap={10}>
                <div>
                    <span></span>       
                    <Space direction="vertical" size={12}>
                        <DatePicker onChange={onChangeDatePicker} needConfirm />
                    </Space>
                </div>
                
                <Input placeholder="" variant="filled" />
                <Input placeholder="" variant="filled" />
                <Input placeholder="" variant="filled" />
                <div>
                    <span>:</span>
                    <InputNumber min={1} max={10} defaultValue={3} />
                    <Space wrap>
                        <Select
                            defaultValue=""
                            style={{ width: 120 }}
                            onChange={handleChange}
                            options={[
                                { value: '', label: '' },
                                { value: '', label: '' },
                                { value: '', label: '' }, 
                                { value: '', label: '' },
                                { value: '', label: '' },
                            ]}
                        />
                    </Space>
                </div>
                <Input placeholder="" variant="filled" />
                <Input placeholder="" variant="filled" />         
                <Input placeholder="(:)"  variant="filled" />                
            </Flex>
            <Flex gap="small" align="flex-start" vertical>
                <Flex gap="small" wrap="wrap">          
                    <Button size={size}></Button>          
                </Flex>        
            </Flex>
            </Card>
            <Card type="inner" title="1" extra={<button>More</button>}>
            <Input placeholder="" variant="borderless" />
            <Input placeholder="" variant="borderless" />
            </Card>
            <Card type="inner" title="2" extra={<button>More</button>}>
            <Input placeholder="" variant="borderless" />
            <Input placeholder="" variant="borderless" />
            </Card>
            <ClearFix height="500px"/>
        </DefaultLayout>
    );
};

export default ItemsList;
